import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import bridgeMock from '@vkontakte/vk-bridge-mock';

const api = isVK() ? new VKMiniAppAPI() : new VKMiniAppAPI(bridgeMock);
api.initApp();

export class vk {
	static async test() {
		api.getUserInfo()
			.then(userInfo => {
				console.log(userInfo);
			})
			.catch(e => console.log(e));
	}

	static getApi(){
		return api
	}

}

function isVK() {
	try {
		return window.self !== window.top;
	} catch (e) {
		return true;
	}
}


