import { VuexModule, Module, Mutation, Action, getModule } from 'vuex-module-decorators';
import store from '@/store';
import {
	detectIsMobile
} from './_utils';

import {MD5} from '@/lib/md5'

export interface IAppState {

}
const api_secret ='PswZKFiEQ8gS0vsa7nLa'

@Module({ dynamic: true, store, name: 'app' })
class App extends VuexModule implements IAppState {
	mobile = detectIsMobile();
	packageVersion = process.env.PACKAGE_VERSION || '0';
	timeBuild = process.env.TIME_OF_BUILD || '';
	_vk_settings = {
		vk_app_id: '',
		vk_user_id: '',
		vk_platform: '',
		viewer_id: -1
	};
	_auth_key = ''

	get auth_key(){
		return this._auth_key
	}

	get isMobile() {
		return this.mobile;
	}

	get appVersion() {
		return this.packageVersion;
	}

	get timeOfBuild() {
		return this.timeBuild;
	}

	get vkSettings() {
		return this._vk_settings;
	}

	get appUrl(){
	  return `/app${this._vk_settings.vk_app_id}_${this._vk_settings.vk_user_id}`
    }

	@Mutation setVkSettings(payload) {
		this._vk_settings = payload;
		this._auth_key = MD5(this._vk_settings.vk_app_id+ '_' +this._vk_settings.vk_user_id+ '_' +api_secret)
	}
}

export const AppModule = getModule(App);
